import React, { useState } from "react";
import {  useNavigate } from "react-router-dom";
import Map from "./Map";
import Carousal from "./component/Carousal";


function Renderer(config) {
    const [numberState, setNumber] = useState(0);
    const navigate = useNavigate()
    const handleRedirect = (path)=>{
     navigate(`/${path}`)   
    }

    const css = ""
    if (typeof config.element !== "undefined" && config?.data?.type != "map" && config?.data?.type != "slider") {
        return React.createElement(
            config.element.type,
            {
                className: `${config.css}`,
                src: config.data && config.data.src && config.data.src,
                onClick:  config?.data?.type == "Link" ? ()=>{handleRedirect(config.data.value)} : ()=>{}
            },
            config.children ?
                config.children.map((c) => Renderer(c)) : config.data && config.data.value
        );
        // }
    } else if (config?.data?.type == "map") {
        return <Map config={config} css={config.css} />
    } else if (config?.data?.type == "slider") {
        return <Carousal config={config} />

    }
}

export default Renderer;


