import React, { useContext, useEffect, useState } from 'react'
import { UserContext } from './context/Context';

export default function Map({ config,css }) {
    return (
        <div className={`${css}`}>
            {
                [...new Array(config.data.loop)].map((value, i) => {
                    return config.children.map((c) => {return <Method config={c}/>}) 
                })
            }
        </div>
    )
}

const Method = ({config})=>{
    return React.createElement(
        config.element.type,
        {
          className: `${config.css}`,
          src: config.data && config.data.src && config.data.src
        },
        config.children ?
          config.children.map((c) => <Method config={c}/>) : config.data && config.data.value
      );
}


